	module.exports = {"data":{"markdownRemark":{"html":"<p>Every few weeks someone asks us when we are going to organize a conference for React. Our answer has always been “some day”. In the mean time, people have been talking about React at other JavaScript conferences around the world. But now the time has finally come for us to have a conference of our own.</p>\n<p><strong>We’re happy to announce <a href=\"http://conf.reactjs.com/\">React.js Conf</a>! It will take place January 28-29, 2015 on Facebook’s campus in Menlo Park, California.</strong></p>\n<p>Before we open registration, <a href=\"http://conf.reactjs.com/call-for-presenters.html\">we’re looking for great talks</a>. We want to see how you pushed application development forward! If you ever talked to a meet-up, pitched React to your co-workers, or done something awesome and want to talk about it, let us know!</p>\n<p>Here are some areas of research we want to explore during the conference if you need some inspiration: server-side rendering, data fetching, language features (eg es6, clojure), immutability, rendering targets (eg svg, canvas), real-time updates…</p>\n<p>We look forward to seeing many of you in person in just a few short months!</p>","excerpt":"Every few weeks someone asks us when we are going to organize a conference for React. Our answer has always been “some day”. In the mean time, people have been talking about React at other JavaScript conferences around the world. But now the time has finally come for us to have a conference of our own. We’re happy to announce  React.js Conf ! It will take place January 28-29, 2015 on Facebook’s campus in Menlo Park, California. Before we open registration,  we’re looking for great talks . We…","frontmatter":{"title":"React.js Conf","next":null,"prev":null,"author":[{"frontmatter":{"name":"Vjeux","url":"https://twitter.com/vjeux"}}]},"fields":{"date":"October 26, 2014","path":"blog/2014-10-27-react-js-conf.md","slug":"/blog/2014/10/27/react-js-conf.html"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"React v16.3.0: New lifecycles and context API"},"fields":{"slug":"/blog/2018/03/29/react-v-16-3.html"}}},{"node":{"frontmatter":{"title":"Update on Async Rendering"},"fields":{"slug":"/blog/2018/03/27/update-on-async-rendering.html"}}},{"node":{"frontmatter":{"title":"Sneak Peek: Beyond React 16"},"fields":{"slug":"/blog/2018/03/01/sneak-peek-beyond-react-16.html"}}},{"node":{"frontmatter":{"title":"Behind the Scenes: Improving the Repository Infrastructure"},"fields":{"slug":"/blog/2017/12/15/improving-the-repository-infrastructure.html"}}},{"node":{"frontmatter":{"title":"Introducing the React RFC Process"},"fields":{"slug":"/blog/2017/12/07/introducing-the-react-rfc-process.html"}}},{"node":{"frontmatter":{"title":"React v16.2.0: Improved Support for Fragments"},"fields":{"slug":"/blog/2017/11/28/react-v16.2.0-fragment-support.html"}}},{"node":{"frontmatter":{"title":"React v16.0"},"fields":{"slug":"/blog/2017/09/26/react-v16.0.html"}}},{"node":{"frontmatter":{"title":"React v15.6.2"},"fields":{"slug":"/blog/2017/09/25/react-v15.6.2.html"}}},{"node":{"frontmatter":{"title":"DOM Attributes in React 16"},"fields":{"slug":"/blog/2017/09/08/dom-attributes-in-react-16.html"}}},{"node":{"frontmatter":{"title":"Error Handling in React 16"},"fields":{"slug":"/blog/2017/07/26/error-handling-in-react-16.html"}}}]}},"pathContext":{"slug":"/blog/2014/10/27/react-js-conf.html"}}

